import { element } from 'prop-types';
import React, { useState } from 'react';
import { Container, Button, FormGroup, FormCheck} from 'react-bootstrap';

function CDRCalculatorComponent() {

    const [totalCdr, setTotalCdr] = useState((0).toFixed(2));
    const [headCdr, setHeadCdr] = useState("");
    const [shoulderCdr, setShoulderCdr] = useState("");
    const [handsCdr, setHandsCdr] = useState("");
    const [waistCdr, setWaistCdr] = useState("");
    const [amuletCdr, setAmuletCdr] = useState("");
    const [ring1Cdr, setRing1Cdr] = useState("");
    const [ring2Cdr, setRing2Cdr] = useState("");
    const [weapon1Cdr, setWeapon1Cdr] = useState("");
    const [weapon2Cdr, setWeapon2Cdr] = useState("");
    const [paragonCdr, setParagonCdr] = useState("");

    const [timeBetweenSkillUse, setTimeBetweenSkillUse] = useState((10).toFixed(2));
    const [skillActiveDuration, setSkillActiveDuration] = useState("");
    const [skillCooldown, setSkillCooldown] = useState("");
    
    const [additionalCDRs, setAdditionalCDRs] = useState([]);

    const calculate = () => {

        let totalCDR = 0, result = 0, timeBetweenSkill = 0, tempAdditionalCDRs = 1;

        if (additionalCDRs != null && additionalCDRs.length > 0) {
            for (let i = 0; i < additionalCDRs.length; i++) {
                if (tempAdditionalCDRs == 1) {
                    tempAdditionalCDRs = ( 1 - (additionalCDRs[i].value/100));
                } else {
                    tempAdditionalCDRs = tempAdditionalCDRs * ( 1 - (additionalCDRs[i].value/100));
                }
            }
            if (tempAdditionalCDRs != 1) {
                tempAdditionalCDRs = tempAdditionalCDRs.toFixed(2);
            }
        }

        console.log(tempAdditionalCDRs)

        totalCDR =(( 1 -  
                        ( 1 - (headCdr/100) ) * ( 1 - (shoulderCdr/100) ) * ( 1 - (handsCdr/100) ) *  ( 1 - (waistCdr/100) ) * 
                        ( 1 - (amuletCdr/100) ) * ( 1 - (ring1Cdr/100) ) * ( 1 - (ring2Cdr/100) ) * ( 1 - (weapon1Cdr/100) ) *
                        ( 1 - (weapon2Cdr/100) ) * ( 1 - (paragonCdr/100) ) * tempAdditionalCDRs) * 100).toFixed(2);
        timeBetweenSkill = (skillCooldown * ( 1 - (totalCDR) / 100 )).toFixed(2);

        if (document.getElementById("cdBeginAfterSkillFinish").checked) {
            console.log(skillActiveDuration);
            result = new Number(parseFloat(timeBetweenSkill.toString()).toFixed(2)) + new Number(parseFloat(skillActiveDuration.toString()).toFixed(2));
        } else {
            result =  timeBetweenSkill;
        }

        setTimeBetweenSkillUse(result);
        setTotalCdr(totalCDR);

    }

    const clearFields = () => {
        setTotalCdr((0).toFixed(2));
        setTimeBetweenSkillUse((10).toFixed(2));

        setHeadCdr("");
        setShoulderCdr("");
        setHandsCdr("");
        setWaistCdr("");
        setAmuletCdr("");
        setRing1Cdr("");
        setRing2Cdr("");
        setWeapon1Cdr("");
        setWeapon2Cdr("");
        setParagonCdr("");

        setSkillActiveDuration("");
        setSkillCooldown("");
        document.getElementById("cdBeginAfterSkillFinish").checked = false;
        if (additionalCDRs != null && additionalCDRs.length > 0) {
            for (let i = 0; i < additionalCDRs.length; i++) {
                document.getElementById("additionalCDR" + i).value = "0";
            }
        }
    }

    const handleAdditionalCDRChange = (index, event) => {
        let tempAdditionalCDRs = [...additionalCDRs];
        tempAdditionalCDRs[index][event.target.name] =  parseFloat(event.target.value);
        setAdditionalCDRs(tempAdditionalCDRs);
    }

    let addAdditionalCDRFields = () => {
        setAdditionalCDRs([...additionalCDRs, {value: 0}]);
    }

    let removeAdditionalCDRFields = (index) => {
        let tempAdditionalCDRs = [...additionalCDRs];
        tempAdditionalCDRs.splice(index, 1);
        setAdditionalCDRs(tempAdditionalCDRs);
    }

    return (
        <Container fluid='lg'>
            {/* Result */}
            <div class='row border border-dark rounded'>
                {/* Total CDR Result */}
                <div class='col-lg-4'  style={{ marginTop: '10px' }}>
                    <p class='fw-bold'>Total Cooldown Reduction</p>
                    <div class='input-group mb-2'>
                        <input id='totalCdrResult' class='form-control' value={totalCdr} type='number' disabled readonly/>
                        <span class='input-group-text'>%</span>
                    </div>
                </div>
                <div class='col-lg-4' style={{ marginTop: '10px' }}>
                    <p class='fw-bold' >Time Between Skill Use</p>
                    <div class='input-group mb-2'>
                        <input id='timeBetweenSkillUseResult' class='form-control' value={timeBetweenSkillUse} type='number' disabled readonly/>
                        <span class='input-group-text'>%</span>
                    </div>
                </div>
                {/*<div class="col-lg-2" style={{ marginTop: '50px' }}>
                    
                </div>*/}
                <div id="calCdrMainBtnGrp" class="col-lg-4">
                    <Button style={{float: 'right', backgroundColor: 'red', borderColor: 'red'}} onClick={clearFields}>Clear Fields</Button>
                    <Button style={{float: 'right', marginRight: '10px', backgroundColor: 'green', borderColor: 'green'}} onClick={calculate}>Calculate</Button>
                </div>                        
            </div>

            {/* Calculators */}
            <div class='row'>
                <div class='col-sm-8 border border-dark rounded' style={{ marginTop: '10px' }}>
                    <p style={{ marginTop: '10px' }}>
                        Max values are only placeholders - feel free to put in whatever values you'd
                        like.
                    </p>

                    <div class='row'>
                        {/* Fixed Column */}
                        <div class='col-lg-6' >
                            <FormGroup>
                                <p class='fw-bold' >
                                    Head
                                </p>
                                <div class='input-group mb-2' >
                                    <input id='headCdr' class='form-control' type='number' placeholder='Max: 25%' value={headCdr}
                                        onChange={e => setHeadCdr(e.target.value)} />
                                    <span class='input-group-text'>%</span>
                                </div>
                            </FormGroup>
                            <FormGroup>
                                <p class='fw-bold' >
                                    Shoulder
                                </p>
                                <div class='input-group mb-2' >
                                    <input id='shoulderCdr' class='form-control' type='number' placeholder='Max: 8%' 
                                        onChange={e => setShoulderCdr(e.target.value)} />
                                    <span class='input-group-text'>%</span>
                                </div>
                            </FormGroup>
                            <FormGroup>
                                <p class='fw-bold' >
                                    Hands
                                </p>
                                <div class='input-group mb-2' >
                                    <input id='handsCdr' class='form-control' type='number' placeholder='Max: 8%' 
                                        onChange={e => setHandsCdr(e.target.value)} />
                                    <span class='input-group-text'>%</span>
                                </div>
                            </FormGroup>
                            <FormGroup>
                                <p class='fw-bold' >
                                    Waist
                                </p>
                                <div class='input-group mb-2' >
                                    <input id='waistCdr' class='form-control' type='number' placeholder='Max: 8%'
                                        onChange={e => setWaistCdr(e.target.value)} />
                                    <span class='input-group-text'>%</span>
                                </div>
                            </FormGroup>
                            <FormGroup>
                                <p class='fw-bold' >
                                    Amulet
                                </p>
                                <div class='input-group mb-2' >
                                    <input id='amuletCdr' class='form-control' type='number' placeholder='Max: 8%'
                                        onChange={e => setAmuletCdr(e.target.value)} />
                                    <span class='input-group-text'>%</span>
                                </div>
                            </FormGroup>
                            <FormGroup>
                                <p class='fw-bold' >
                                    Ring 1
                                </p>
                                <div class='input-group mb-2' >
                                    <input id='ring1Cdr' class='form-control' type='number' placeholder='Max: 8%'
                                        onChange={e => setRing1Cdr(e.target.value)} />
                                    <span class='input-group-text'>%</span>
                                </div>
                            </FormGroup>
                            <FormGroup>
                                <p class='fw-bold' >
                                    Ring 2
                                </p>
                                <div class='input-group mb-2' >
                                    <input id='ring2Cdr' class='form-control' type='number' placeholder='Max: 8%'
                                        onChange={e => setRing2Cdr(e.target.value)} />
                                    <span class='input-group-text'>%</span>
                                </div>
                            </FormGroup>
                            <div style={{height: '10px'}}></div>
                        </div>

                        {/* Fixed Column */}
                        <div class='col-lg-6'>
                            <FormGroup>
                                <p class='fw-bold'>
                                    Weapon 1
                                </p>
                                <div class='input-group mb-2' style={{ marginTop: '10px' }}>
                                    <input id='weapon1Cdr' class='form-control' type='number' placeholder='Max: 10%'
                                        onChange={e => setWeapon1Cdr(e.target.value)} />
                                    <span class='input-group-text'>%</span>
                                </div>
                            </FormGroup>
                            <FormGroup>
                                <p class='fw-bold'>
                                    Weapon 2
                                </p>
                                <div class='input-group mb-2'>
                                    <input id='weapon2Cdr' class='form-control' type='number' placeholder='Max: 10%' 
                                        onChange={e => setWeapon2Cdr(e.target.value)} />
                                    <span class='input-group-text'>%</span>
                                </div>
                            </FormGroup>
                            <FormGroup>
                                <p class='fw-bold' style={{ marginTop: '10px' }}>
                                    Paragon
                                </p>
                                <div class='input-group mb-2' style={{ marginTop: '10px' }}>
                                    <input id='paragonCdr' class='form-control' type='number' placeholder='Max: 10%' 
                                        onChange={e => setParagonCdr(e.target.value)} />
                                    <span class='input-group-text'>%</span>
                                </div>
                            </FormGroup>
                            {additionalCDRs.map((element, index) => (
                                <FormGroup key={index}>
                                    <p class='fw-bold' style={{ marginTop: '10px' }}>
                                        Additional CDR
                                    </p>
                                    <div class='input-group mb-3'>
                                        <input class='form-control' id={"additionalCDR" + index}  name="value" type='number' placeholder="0" onChange={e => handleAdditionalCDRChange(index, e)}/>
                                        <span class='input-group-text'>%</span>
                                        <Button style={{marginLeft: '10px', backgroundColor: 'gray', borderColor: 'gray'}} onClick={() => removeAdditionalCDRFields(index)}>Remove</Button>
                                    </div>
                                </FormGroup>
                            ))}
                            <FormGroup>
                                <Button onClick={() => addAdditionalCDRFields()}>Add fields</Button>
                            </FormGroup>
                            <div style={{height: '10px'}}></div>
                        </div>
                    </div>
                </div>
                <div class='col-sm-1'/>
                <div class='col-sm-3 border border-dark rounded' style={{ marginTop: '10px' }}>
                    
                    <p style={{ marginTop: '10px' }}>
                        Skill Information
                    </p>

                    <div class='row'>
                        <div class="col-sm-12">
                            <FormGroup>
                                <p class='fw-bold' >
                                    Skill Active Duration
                                </p>
                                <div class='input-group mb-2' >
                                    <input id='skillActiveDuration' class='form-control' type='number' value={skillActiveDuration}
                                        onChange={e => setSkillActiveDuration(e.target.value)} />
                                    <span class='input-group-text'>sec</span>
                                </div>
                            </FormGroup>
                            <FormGroup>
                                <p class='fw-bold' >
                                    Skill Cooldown
                                </p>
                                <div class='input-group mb-2' >
                                    <input id='skillCooldown' class='form-control' type='number' value={skillCooldown}
                                        onChange={e => setSkillCooldown(e.target.value)} />
                                    <span class='input-group-text'>sec</span>
                                </div>
                            </FormGroup>
                            <FormGroup>
                                <p class='fw-bold' >
                                    Cooldown Begins After Skill Finishes
                                </p>
                                <FormCheck>
                                    <input class="form-check-input" type="checkbox"  id="cdBeginAfterSkillFinish" />
                                    <label class="form-check-label" for="cdBeginAfterSkillFinish">
                                        Yes
                                    </label>
                                </FormCheck>
                            </FormGroup>
                        </div>
                    </div>
                </div>
            </div>
        </Container>
    );
}

export default CDRCalculatorComponent;
