import React, { Suspense, lazy } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import menumap from '../resources/menumap';
import LoadingComponent from '../components/loading';
import CDRCalculatorComponent from './calculator/CDRCalculatorComponent';
import RCRCalculatorComponent from './calculator/RCRCalculatorComponent';
import GemCalculatorComponent from './calculator/GemCalculatorComponent';
import ParagonCalculatorComponent from './calculator/ParagonCalculatorComponent';
import FollowerSettingComponent from './ros/FollowerSettingComponent';
import EfficiencySettingComponent from './ros/EfficiencySettingComponent';
import RBATriggerComponent from './ros/RBATriggerComponent';
import TroubleshootingComponent from './ros/TroubleShootingComponent';
import ThudPluginComponent from './thud-plugin/ThudPluginComponent';
import BluePostComponent from './bluepost/BluePostComponent';
import NotFountComponent from './404/NotFountComponent';

const DashboardComponent = lazy(() => import('./dashboard'));

function Router() {
    return (
        <Suspense fallback={<LoadingComponent loading />}>
            <Switch>
                <Route exact path="/" component={DashboardComponent}/>
                <Route path={menumap.dashboard} component={DashboardComponent}/>
                <Route path={menumap.calculator_cdr} component={CDRCalculatorComponent} />
                <Route path={menumap.calculator_rcr} component={RCRCalculatorComponent} />
                <Route path={menumap.calculator_gems} component={GemCalculatorComponent} />
                <Route path={menumap.calculator_paragon} component={ParagonCalculatorComponent} />
                <Route path={menumap.ros_followersettings} component={FollowerSettingComponent} />
                <Route path={menumap.ros_efficiency_setting} component={EfficiencySettingComponent} />
                <Route path={menumap.ros_rbatriggers} component={RBATriggerComponent} />
                <Route path={menumap.ros_troubleshooting} component={TroubleshootingComponent} />
                <Route path={menumap.thudplugin} component={ThudPluginComponent} />
                <Route path={menumap.bluepost} component={BluePostComponent} />
                <Route path="*" component={NotFountComponent}/>
            </Switch>
        </Suspense>
    );
}

export default Router;
